<template>
    <div>
        <van-nav-bar
        title="注册"
        left-text="返回" 
        @click-left="tojoin"
        />
        <div class="logo">
            <img class="auto-img" src="../assets/images/loginImg.jpg" alt="">
        </div>
        <van-form @submit="register">
            <van-field
                type="text"
                label="手机号"
                placeholder="请输入11位手机号"
                left-icon="phone-o"                  
                v-model="massge.value1"
                name="pattern"
                
            >
            </van-field>
            <van-field
                type="text"
                label="昵称"
                placeholder="昵称(2-8个字符)"
                left-icon="contact"
                v-model="massge.value2"
                name="namecl"
                
            />
            <van-field
                :type="isEye ? 'password':'text'"
                label="密码"
                placeholder="以字母开头(请输入6-16位字符)"
                left-icon="lock"
                :right-icon="isEye ? 'closed-eye' : 'eye-o'"
                v-model="massge.value3"
                name="pswcl"
                @click-right-icon='toggle()'
            />
            <div class="register-btn">
                <van-button round block type="info" native-type="submit">立即注册</van-button>
            </div>
            
        </van-form>
        
    </div>
</template>

<script>
import { Toast } from 'vant';
import formCl from '../assets/js/formCl'
    export default {
        data() {
            return {
                massge:{
                    value1: '',
                    value2: '',
                    value3: '',
                } ,
                isEye:true,
                
            };
        },
        methods: {
            tojoin(){
                this.$router.go(-1)
            },
            toggle(){
                this.isEye = !this.isEye
            },
            
            register(){
                if(formCl.Verify(this.massge,this)){
                    //复制对象
                    let data = {
                        appkey:this.appkey,
                        nickName:this.massge.value2,
                        password:this.massge.value3,
                        phone:this.massge.value1
                    }

                    //请求密钥
                    data.appkey = this.appkey;

                    //开启加载提示
                    this.$toast.loading({
                        message: '加载中...',
                        forbidClick: true,

                        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                        duration:0
                    });

                    //发起注册请求
                    this.axios({
                        method: 'POST',
                        url: '/register',

                        //post传递参数需要写在data
                        data
                    }).then(result => {
                        //关闭加载提示
                        this.$toast.clear();
                        this.$router.push({name:'Login'})
                        
                    }).catch(err => {
                        //关闭加载提示
                        this.$toast.clear();
                        
                    })
                }
            }
        },
    }
</script>

<style lang="less" scoped>

.logo{
    width: 100%;
    
    margin:  auto;
    img{
        width: 100%;
        height: 100%;
    }
}
/deep/.van-nav-bar{
    background-color: #f8f0b1;
}
/deep/.van-nav-bar__text {
    color: #f4b973;
}
/deep/ .van-button--info {
    margin-top: 30px;
    color: #fff;
    background-color: #f4b973;
    border: 1px solid #f4b973;
}
    
</style>